"use client";
import { useMemo } from "react";
import { Dispatch, SetStateAction, useState } from "react";
import { formDataType } from "./page";
import SpinLoader from "@/components/preloader/SpinLoader";
import GithubIcons from "@/components/icons/GithubIcons";
import HoverTitle from "@/components/HoverTitle";

export default function FilterOption({
  keyName,
  formData,
  setFormData,
}: {
  keyName: string;
  formData: formDataType;
  setFormData: Dispatch<SetStateAction<formDataType>>;
}) {
  return useMemo(
    () => (
      <_FilterOption
        keyName={keyName}
        formData={formData}
        setFormData={setFormData}
      />
    ),
    [formData, setFormData]
  );
}

function _FilterOption({
  keyName,
  formData,
  setFormData,
}: {
  keyName: string;
  formData: formDataType;
  setFormData: Dispatch<SetStateAction<formDataType>>;
}) {
  function switchSelection() {
    setFormData((prev) => ({
      ...prev,
      [keyName]: {
        value:
          prev[keyName].value === "all"
            ? true
            : prev[keyName].value === true
            ? false
            : "all",
        isLoading: true,
      },
    }));
  }

  if (formData[keyName].isLoading) return <SpinLoader size={28} />;
  else
    return (
      <HoverTitle title="Filter option">
        {formData[keyName].value === "all" ? (
          <div
            className="w-4 h-4 p-0.5 rounded-full cursor-pointer bg-gray-100 dark:bgBlack3 hover:ring-4 hover:ring-gray-200  dark:hover:ring-gray-600 border border-gray-300"
            onClick={switchSelection}
          ></div>
        ) : formData[keyName].value ? (
          <div
            className="w-fit h-fit p-0.5 rounded-full cursor-pointer bg-green-100 dark:bgBlack3 hover:ring-4 hover:ring-green-200 dark:hover:ring-gray-600"
            onClick={switchSelection}
          >
            <GithubIcons.tik className="fill-green-600" />
          </div>
        ) : (
          <div
            className="w-fit h-fit p-0.5 rounded-full cursor-pointer bg-red-100 dark:bgBlack3 hover:ring-4 hover:ring-red-200 dark:hover:ring-gray-600"
            onClick={switchSelection}
          >
            <GithubIcons.close className="fill-red-600" />
          </div>
        )}
      </HoverTitle>
    );
}
