import GithubIcons from "@/components/icons/GithubIcons";

export default function CheckBox({
  isChecked,
  onClick,
}: {
  isChecked: boolean;
  onClick: Function;
}) {
  if (isChecked)
    return (
      <div
        className="w-fit h-fit p-0.5 rounded-full cursor-pointer bg-green-100 dark:bgBlack3 hover:ring-4 hover:ring-green-200 dark:hover:ring-gray-600"
        onClick={() => onClick()}
      >
        <GithubIcons.tik className="fill-green-600" />
      </div>
    );
  else
    return (
      <div
        className="w-fit h-fit p-0.5 rounded-full cursor-pointer bg-red-100 dark:bgBlack3 hover:ring-4 hover:ring-red-200 dark:hover:ring-gray-600"
        onClick={() => onClick()}
      >
        <GithubIcons.close className="fill-red-600" />
      </div>
    );
}
