"use client";
import {
  Dispatch,
  SetStateAction,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import Console from "@/utils/console";
import useGlobalContext, {
  countSummaryType,
  myAccessType,
  navSearchBarType,
} from "../GlobalContext";
import { hideLoader } from "../BlankPageLoader";
import { mutate, useSWR } from "@/utils/api-handler/swr";
import { isNull } from "@/utils/more";
import axios from "@/utils/api-handler/axios";
import SpinLoader from "@/components/preloader/SpinLoader";
import Image from "next/image";
import $$ from "@/utils/global-variables";
import SvgCat from "@/components/svg-image/Cat";
import api from "@/app/api/endpoints";
import CheckBox from "./CheckBox";
import SelectType from "./FilterOption";
import HoverTitle from "@/components/HoverTitle";

export type formDataType = {
  createUser: { value: boolean | "all"; isLoading: boolean };
  editUser: { value: boolean | "all"; isLoading: boolean };
  deleteUser: { value: boolean | "all"; isLoading: boolean };
  createFaculty: { value: boolean | "all"; isLoading: boolean };
  editFaculty: { value: boolean | "all"; isLoading: boolean };
  deleteFaculty: { value: boolean | "all"; isLoading: boolean };
  createSession: { value: boolean | "all"; isLoading: boolean };
  editSession: { value: boolean | "all"; isLoading: boolean };
  deleteSession: { value: boolean | "all"; isLoading: boolean };
  editAccess: { value: boolean | "all"; isLoading: boolean };
  viewOthersHistory: { value: boolean | "all"; isLoading: boolean };
  [key: string]: { value: boolean | "all"; isLoading: boolean };
};
export default function Page() {
  const {
    myAccess,
    setActiveTab,
    countSummary,
    doRefetch,
    navSearchBar,
    setNavSearchBar,
  } = useGlobalContext();
  return useMemo(
    () => (
      <_Page
        myAccess={myAccess}
        setActiveTab={setActiveTab}
        countSummary={countSummary}
        doRefetch={doRefetch}
        navSearchBar={navSearchBar}
        setNavSearchBar={setNavSearchBar}
      />
    ),
    [
      myAccess,
      setActiveTab,
      countSummary,
      doRefetch,
      navSearchBar,
      setNavSearchBar,
    ]
  );
}

function _Page({
  myAccess,
  setActiveTab,
  countSummary,
  doRefetch,
  navSearchBar,
  setNavSearchBar,
}: {
  myAccess: myAccessType;
  setActiveTab: Dispatch<SetStateAction<string>>;
  countSummary: countSummaryType;
  doRefetch: boolean;
  navSearchBar: navSearchBarType;
  setNavSearchBar: Dispatch<SetStateAction<navSearchBarType>>;
}) {
  Console.component("Page");

  useEffect(() => {
    document.title = "Access";
    setActiveTab("access");
  }, [setActiveTab]);

  const [formData, setFormData] = useState<formDataType>({
    createUser: { value: "all", isLoading: false },
    editUser: { value: "all", isLoading: false },
    deleteUser: { value: "all", isLoading: false },
    createFaculty: { value: "all", isLoading: false },
    editFaculty: { value: "all", isLoading: false },
    deleteFaculty: { value: "all", isLoading: false },
    createSession: { value: "all", isLoading: false },
    editSession: { value: "all", isLoading: false },
    deleteSession: { value: "all", isLoading: false },
    editAccess: { value: "all", isLoading: false },
    viewOthersHistory: { value: "all", isLoading: false },
  });
  const [data, setData] = useState<
    | {
        id: number;
        name: string;
        email: string;
        image: string;
        createUser: boolean;
        editUser: boolean;
        deleteUser: boolean;
        createFaculty: boolean;
        editFaculty: boolean;
        deleteFaculty: boolean;
        createSession: boolean;
        editSession: boolean;
        deleteSession: boolean;
        editAccess: boolean;
        viewOthersHistory: boolean;
      }[]
    | undefined
  >(undefined);
  const [haveMoreData, setHaveMoreData] = useState<boolean>(true);
  const [showRefetchLoader, setShowRefetchLoader] = useState<boolean>(false);
  const [limit, setLimit] = useState<number>(50);

  /*****
   * Get all users data
   ******/
  const [res, mutateAllUser] = useSWR(
    api.readAllUsersAccess +
      "?limit=" +
      limit +
      "&&searchKey=" +
      navSearchBar.keyword.trim() +
      "&&createUser=" +
      formData.createUser.value +
      "&&editUser=" +
      formData.editUser.value +
      "&&deleteUser=" +
      formData.deleteUser.value +
      "&&createFaculty=" +
      formData.createFaculty.value +
      "&&editFaculty=" +
      formData.editFaculty.value +
      "&&deleteFaculty=" +
      formData.deleteFaculty.value +
      "&&createSession=" +
      formData.createSession.value +
      "&&editSession=" +
      formData.editSession.value +
      "&&deleteSession=" +
      formData.deleteSession.value +
      "&&editAccess=" +
      formData.editAccess.value +
      "&&viewOthersHistory=" +
      formData.viewOthersHistory.value
  );
  useEffect(() => {
    if (!res) return;
    if (res.status === 200) {
      setData(res.data);
      setHaveMoreData(res.haveMoreData);
      setNavSearchBar((prev) => ({ ...prev, isLoading: false }));
      setShowRefetchLoader(false);
    }

    // Making all loading false
    const newData = { ...formData };
    for (const i in newData) {
      const record = newData[i];
      if (record.isLoading === true) newData[i].isLoading = false;
    }
    setFormData(newData);
    hideLoader();
  }, [res]);

  /*****
   * Reload data fetchers
   ******/
  useEffect(() => {
    if (doRefetch) mutateAllUser();
  }, [doRefetch, mutateAllUser]);

  /*****
   * Refetching record on scroll
   ******/
  const observerTarget = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0] && entries[0].isIntersecting) {
          if (haveMoreData) {
            setShowRefetchLoader(true);
            setLimit((prev) => prev + 50);
          }
        }
      },
      { threshold: 0.1 }
    );

    const el = observerTarget.current;
    if (haveMoreData && isNull(navSearchBar.keyword) && !showRefetchLoader) {
      if (el) observer.observe(el);
    } else {
      if (el) observer.unobserve(el);
    }

    return () => {
      if (el) observer.unobserve(el);
    };
  }, [
    observerTarget,
    data,
    navSearchBar.keyword,
    showRefetchLoader,
    countSummary.teachers.total,
    setLimit,
    haveMoreData,
  ]);

  /*****
   * Updating access
   ******/
  const [updatingId, setUpdatingId] = useState<string[]>([]);
  async function doUpdate(id: number, keyName: string, value: boolean) {
    if (!myAccess?.editAccess) return;

    const loaderId = `${id}_${keyName}`;
    setUpdatingId((prev) => [...prev, loaderId]);
    await axios.post(api.updateAccess, { id, keyName, value });
    await Promise.all([mutateAllUser(), mutate(api.readMyAllAccess)]);
    setUpdatingId((prev) => prev.filter((id) => id != loaderId));
  }

  return (
    <div className="w-full h-full px-8 py-6 bg-white dark:bgBlack2 rounded-lg">
      {/* Title */}
      <div className="flex items-center gap-2 mb-2 text-gray-600">
        <div className="fontGs text-2xl">Access management</div>
      </div>

      {/* Table header */}
      <div className="px-2 py-2.5 pr-6 flex items-center gap-2 border-b border-gray-300 sticky bg-white dark:bgBlack2 top-[64px]">
        <div className="w-[300px] pl-4 fontGsm">Name</div>
        <div className="w-[200px] fontGsm">Email</div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Create user" className="z-2">
            <div>Create user</div>
          </HoverTitle>
          <SelectType
            keyName="createUser"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Edit user" className="z-2">
            <div>Edit user</div>
          </HoverTitle>
          <SelectType
            keyName="editUser"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Delete user" className="z-2">
            <div>Delete user</div>
          </HoverTitle>
          <SelectType
            keyName="deleteUser"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Create faculty" className="z-2">
            <div>Create faculty</div>
          </HoverTitle>
          <SelectType
            keyName="createFaculty"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Edit faculty" className="z-2">
            <div>Edit faculty</div>
          </HoverTitle>
          <SelectType
            keyName="editFaculty"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Delete faculty" className="z-2">
            <div>Delete faculty</div>
          </HoverTitle>
          <SelectType
            keyName="deleteFaculty"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Create session" className="z-2">
            <div>Create session</div>
          </HoverTitle>
          <SelectType
            keyName="createSession"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Edit session" className="z-2">
            <div>Edit session</div>
          </HoverTitle>
          <SelectType
            keyName="editSession"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Delete session" className="z-2">
            <div>Delete session</div>
          </HoverTitle>
          <SelectType
            keyName="deleteSession"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="Edit access" className="z-2">
            <div>Edit access</div>
          </HoverTitle>
          <SelectType
            keyName="editAccess"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
        <div className="w-[100px] text-center fontGsm flex flex-col items-center gap-1">
          <HoverTitle title="View others history" className="z-2">
            <div>View Others History</div>
          </HoverTitle>
          <SelectType
            keyName="viewOthersHistory"
            formData={formData}
            setFormData={setFormData}
          />
        </div>
      </div>

      {data !== undefined && (
        <div>
          <div className="pl-6 p-4 pb-3 flex justify-between items-center">
            {/* Summary */}
            <div className="flex items-center gap-5">
              <div className="fontGsm text-gray-500">
                Total {data.length}/
                {countSummary.teachers.total +
                  countSummary.students.total +
                  countSummary.others.total}
              </div>
            </div>

            {/* Search keys */}
            <div className="flex justify-center items-center gap-4"></div>
          </div>

          {isNull(data) ? (
            <div className="w-full flex justify-center items-center py-16">
              <div className="w-fit h-fit flex flex-col justify-center items-center gap-6">
                <SvgCat className="w-[170px]" />
                <div className="fontGsm text-gray-500">No record found</div>
              </div>
            </div>
          ) : (
            <div className="h-fit">
              {data.map((record, index) => {
                return (
                  <div
                    key={index}
                    className="group/menu px-2 flex items-center gap-2 hover:bgLightBlue dark:hover:bgBlack3 cursor-pointer rounded-md"
                  >
                    <div className="w-[300px] pl-4 py-2.5">
                      <div className="flex items-center gap-4">
                        <Image
                          src={record.image || $$.defaultUserImg}
                          className="rounded-full"
                          width="32"
                          height="32"
                          alt="Profile image"
                        />
                        <div className="break-all cursor-pointer">
                          {record.name}
                        </div>
                      </div>
                    </div>
                    <div className="w-[200px] break-all py-2.5">
                      {record.email}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_createUser`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.createUser}
                          onClick={() =>
                            doUpdate(record.id, "createUser", record.createUser)
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_editUser`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.editUser}
                          onClick={() =>
                            doUpdate(record.id, "editUser", record.editUser)
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_deleteUser`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.deleteUser}
                          onClick={() =>
                            doUpdate(record.id, "deleteUser", record.deleteUser)
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_createFaculty`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.createFaculty}
                          onClick={() =>
                            doUpdate(
                              record.id,
                              "createFaculty",
                              record.createFaculty
                            )
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_editFaculty`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.editFaculty}
                          onClick={() =>
                            doUpdate(
                              record.id,
                              "editFaculty",
                              record.editFaculty
                            )
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_deleteFaculty`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.deleteFaculty}
                          onClick={() =>
                            doUpdate(
                              record.id,
                              "deleteFaculty",
                              record.deleteFaculty
                            )
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_createSession`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.createSession}
                          onClick={() =>
                            doUpdate(
                              record.id,
                              "createSession",
                              record.createSession
                            )
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_editSession`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.editSession}
                          onClick={() =>
                            doUpdate(
                              record.id,
                              "editSession",
                              record.editSession
                            )
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_deleteSession`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.deleteSession}
                          onClick={() =>
                            doUpdate(
                              record.id,
                              "deleteSession",
                              record.deleteSession
                            )
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_editAccess`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.editAccess}
                          onClick={() =>
                            doUpdate(record.id, "editAccess", record.editAccess)
                          }
                        />
                      )}
                    </div>
                    <div className="w-[100px] flex justify-center break-all py-2.5">
                      {updatingId.includes(`${record.id}_viewOthersHistory`) ? (
                        <SpinLoader size={28} />
                      ) : (
                        <CheckBox
                          isChecked={record.viewOthersHistory}
                          onClick={() =>
                            doUpdate(
                              record.id,
                              "viewOthersHistory",
                              record.viewOthersHistory
                            )
                          }
                        />
                      )}
                    </div>
                  </div>
                );
              })}
              <div ref={observerTarget}></div> {/* When data found */}
            </div>
          )}
        </div>
      )}
      {showRefetchLoader === true && (
        <div className="w-full flex justify-center py-4">
          <SpinLoader />
        </div>
      )}
    </div>
  );
}
